// Font Sizes
.sm-font { font-size: rem(14px); }
.md-font { font-size: rem(20px); }
.lg-font { font-size: rem(30px); }
.xl-font { font-size: rem(40px); }

// Positioning
.flex {
    display: flex;
    &.column { flex-direction: column; }
    &.justify-center { justify-content: center; }
    &.align-center { align-items: center; }
    &.wrap { flex-wrap: wrap; }
    &.space-between { justify-content: space-between; }
    &.space-around { justify-content: space-around; }
    &.auto-center {
        justify-content: center;
        align-items: center;
    }
    > *.full-grow { flex: 1; }
}

.grid{
    display: grid;
}

.text-center { text-align: center; }
.text-right { text-align: right; }
.text-left { text-align: left; }

.center-block {
    display: block;
    margin: 0 auto;
}

// Media helpers
.square-ratio { @include aspect-ratio(1, 1); }

.wide-ratio { @include aspect-ratio(16, 9); }

.square-3 { @include aspect-ratio(4, 3); }


// Buttons
.simple-button {
    background: transparent;
    color: black;
    font-size: inherit;
    font-family: inherit;
    border: 0;
    opacity: 0.6;
    transition: 0.3s all;
    padding: 0;
    cursor: pointer;

    &:disabled {
        cursor: initial;
        opacity: 0.2;
    }

    i {
        display: inline-block;
        width: 100%;
        height: 100%;
    }

    &:hover:not(:disabled) {
        opacity: 1;
    }
}

.medium-button { font-size: 1.5rem; }

// Transitions
.fast-trans { transition: 0.25s all; }
.normal-trans { transition: 0.4s all; }
.slow-trans { transition: 0.7s all; }


// Normalize Background
.normBackground {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

// Dividing
.bottom-divider {
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid lightgray;
}
